.search-field {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    padding-bottom: 20px;
    border-bottom: 2px solid #e27a3f;
}

.search-field button {
    padding: 5px 15px;
    background: #e27a3f;
    color: rgba(255,255,255, 0.75);
    font-size: 1rem;
    cursor: pointer;
    -webkit-transition: color 0.2s ease-out;
    -o-transition: color 0.2s ease-out;
    transition: color 0.2s ease-out;
    outline: 0;
    border: 0;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    color: white;
    border-radius: 0 3px 3px 0;
}

.search-box {
    border-radius: 3px 0 0 3px;
    padding-left: 5px;
}

.search-box:focus {
    border-color: #e27a3f;
    outline: 0;
}


