.sign-in-container {
    -ms-flex-preferred-size: 100%;
        flex-basis: 100%;
    width: 100%;
    padding: 20px 0;
}

.sign-in-container div {
    margin: 10px 0;
}

/* shared with SignUp.css */
.sign-form {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    text-align: center;
}
.sign-form h1 { 
    color: #e27a3f;
}
.form-container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
}
.auth-button {
    padding: 2px 5px;
    background: #e27a3f;
    color: rgba(255,255,255, 0.75);
    font-size: 1rem;
    cursor: pointer;
    -webkit-transition: color 0.2s ease-out;
    -o-transition: color 0.2s ease-out;
    transition: color 0.2s ease-out;
    outline: 0;
    border: 0;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    color: white;
    border-radius: 3px;
}
div input {
    border-radius: 3px 0 0 3px;
    padding-left: 5px;
}
div input:focus {
    border-color: #e27a3f;
    outline: 0;
}


